@media (prefers-color-scheme: light) {
  :root {
    --text: #040c1a;
    --background: #f2f6fd;
    --primary: #4f33cc;
    --secondary: #bcd1f5;
    --accent: #b6d043;
  }
}
@media (prefers-color-scheme: dark) {
  :root {
    --text: #e5edfb;
    --background: #0c1524;
    --primary: #4f33cc;
    --secondary: #0a1f43;
    --accent: #a2bc2f;
  }
}
::selection {
  background-color: var(--accent);
}

body {
  background-color: var(--background);
}

#root {
  display: flex;
  flex-direction: column;
  color: var(--text);
}

header {
  margin: 0 auto;
  padding: 2rem;
}

input {
  min-width: 25vw;
  resize: horizontal;
  padding: 0.5rem;
  border-radius: 25rem;
  border: 3px solid var(--primary);
  margin: 0 2rem;
  font-size: 1em;
  transition: border-color 250ms;
}
input:focus {
  outline: none;
  border-color: var(--secondary);
}

.buttonList {
  padding: 2rem;
  display: grid;
  gap: 0.5rem;
  grid-template-columns: repeat(auto-fit, minmax(min(10rem, 100%), 1fr));
}
.buttonList button {
  border-radius: 0.25rem;
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 1em;
  background-color: var(--secondary);
  cursor: pointer;
}

.stop-sounds {
  display: block;
  margin: auto;
  border-radius: 0.25rem;
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 2em;
  color-scheme: dark;
  background-color: var(--primary);
  cursor: pointer;
}